html {
  background-color:#F9F6F1;
}

.containers {
  padding-left: 300px;
  padding-right: 300px;
  padding-top: 40px
}

/* Contains title */
.firstContainer {
  color: black;
}

.secondContainer {
  color: #1b1b1bbf;
  text-align: left;
  padding-right: 100px;
  margin-top: 40px;
}

.secondContainer p{
  font-size: 25px;
  margin: 0px;
  padding-bottom: 25px;
}

.media-icons {
  padding-top: 50px;
}

.media-icons img{
  height: 75px;
  width: 75px;
  padding-right: 35px;
  transition: all .2s ease-in-out;
}

.media-icons img:hover{
  height: 80px;
  width: 80px;
  rotate: -10deg;
}

.media-icons a:focus {
  outline: none !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
